<template>
    <div id="banner">
        <v-headpanel>
            <em>首页banner更换</em>渠道分组及渠道修改操作。
        </v-headpanel>
        <el-row>
            <el-col
                :span="5"
                v-for="(o, index) in bannerlist"
                :key="index"
                :offset="index > 0 ? 1 : 0"
            >
                <el-card :body-style="{ padding: '0px' }">
                    <img
                        :src="website+'/imgs/index/'+o.name+'?'+date"
                        class="image"
                    >
                    <div class="buttonbar">
                        <span class="title">滚屏图片{{index+1}}</span>
                        <v-upload
                            @upload="updateImg"
                            :name="'banner'+(index+1)"
                            :url="url"
                        ></v-upload>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import Headpanel from "../components/Headpanel.vue";
import Upload from "../components/Upload.vue";
import { mapState } from "vuex";

export default {
    data() {
        return {
            url: "banner",
            bannerlist: [],
            date: new Date().getTime()
        };
    },
    methods: {
        updateImg: function(file) {
            let id = file.slice(6, 7);
            console.log(file);
            console.log(id);
            console.log(this.bannerlist);

            this.bannerlist.splice(id - 1, 1, { id: id, name: file });
            console.log(this.bannerlist);
            this.date = new Date().getTime();
        }
    },
    created() {
        return this.get("bannerlist")
            .then(res => {
                this.bannerlist = res;
            })
            .catch(e => {
                this.$message({
                    type: "warning",
                    message: "获取列表失败!失败原因:" + e
                });
            });
    },
    computed: {
        ...mapState(["website"])
    },
    components: {
        "v-headpanel": Headpanel,
        "v-upload": Upload
    }
};
</script>

<style lang='scss'>
#banner {
    .button {
        padding: 0;
        float: right;
    }

    .image {
        width: 100%;
        display: block;
    }
    .buttonbar {
        padding: 14px;
        font-size: 18px;
        .title {
            line-height: 34px;
        }
        span {
            font-size: 18px;
            // line-height: 48px;
        }
        .upload {
            float: right;
            margin-bottom: 12px;
        }
    }
}
</style>
